.crypto_button {
  box-sizing: border-box;
  border: 1px #d7dada solid;
  border-radius: 3px;
  background-image: none;
  text-decoration: none;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 12px;
  font-weight: bold;
  line-height: 1.5;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  min-height: 1px;
  min-width: 1px;
  max-height: 32px;
  margin: 0;
  padding: 6px 15px;
  color: #000000;
  background-color: #f4f5f5;
  background: -moz-linear-gradient(top, #f4f5f5 0%, #dfdddd 100%);
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    color-stop(0%, #f4f5f5),
    color-stop(100%, #dfdddd)
  );
  background: -webkit-linear-gradient(top, #f4f5f5 0%, #dfdddd 100%);
  background: -o-linear-gradient(top, #f4f5f5 0%, #dfdddd 100%);
  background: -ms-linear-gradient(top, #f4f5f5 0%, #dfdddd 100%);
  background: linear-gradient(to bottom, #f4f5f5 0%, #dfdddd 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#F4F5F5", endColorstr="#DFDDDD",GradientType=0 );
}

.crypto_button:disabled,
.crypto_button:disabled:hover,
.crypto_button[disabled],
.crypto_button[disabled]:hover {
  margin: 0;
  color: #1a1a1a;
  background-image: none;
  background-color: #f5f6f6;
  cursor: default;
  border-color: #d7dada;
}

